import React from 'react';
import {Bars, Nav,NavIcon,NavLink} from './NavbarElement';

const Navbar = ({toggle}) => {
    return (
        <>
        <Nav>
        <NavLink to='/'>eatNow</NavLink>
        <NavIcon onClick={toggle}>
            <p className='so'>Menu</p>
            <Bars/>
        </NavIcon>
        </Nav>
        
            
        </>
    );
};

export default Navbar;
