import React,{useState} from 'react';
import {HeroContainer,HeroContent,HeroH1,HeroItems,HeroP,HeroBtn,Space} from './HeroElement';
import Navbar from '../Nabvar/Navbar';
import Sidebar from '../../Sidebar'

const Hero = () => {
    const [isOpen, setIsOpen] = useState(false)

    const toggle = () => {
        setIsOpen(!isOpen);
    }

    return (
        <HeroContainer>
            <Navbar toggle={toggle}/>
            <Sidebar isOpen={isOpen} toggle={toggle}/>
            <HeroContent>
                <HeroItems>
                    <HeroH1>Best food ever</HeroH1>
                    <Space/>
                    <HeroP>Within Seconds,Fast And Reliable</HeroP>
                    <Space/>
                    <Space/>
                    <HeroBtn>Place Order</HeroBtn>
                </HeroItems>
            </HeroContent>

        </HeroContainer>
    )
}

export default Hero
