import React from 'react';
import {FeatureContainer,FeatureButton,FeaCon} from '../Feature/FeatureElement';

const Feature = () => {
    return (
       <FeatureContainer>
           <h1>Food of the day</h1>
           <p>The best taste of food you can ever have</p>
           <FeaCon>
           <FeatureButton>Order Now</FeatureButton>
           </FeaCon>
           
       </FeatureContainer>
    )
}

export default Feature
