import React from 'react';
import {SidebarContainer,SidebarLink,SidebarMenu,SidebarRoute,SideBtnWrap,ClosedIcon,Icon} from './SidebarElement';

const Sidebar = ({isOpen,toggle}) => {
    return (
      <SidebarContainer isOpen={isOpen} onClick={toggle}>
          <Icon>
              <ClosedIcon/>
          </Icon>
          <SidebarMenu>
              <SidebarLink to='/Food'>Food</SidebarLink>
              <SidebarLink to='/Cake'>Cake</SidebarLink>
              <SidebarLink to='/Snack'>Snack</SidebarLink>
 
          </SidebarMenu>
          <SideBtnWrap>
              <SidebarRoute to='/'>Order Now</SidebarRoute>
          </SideBtnWrap>
      </SidebarContainer>
    )
}

export default Sidebar
